import React, { useState } from "react"

import {
    Nav,
    NavItem,
    NavLink,
    TabContent,
    TabPane,
} from "reactstrap"

import classnames from "classnames"

// import AssessmentFindingsTabContent from "./tabs/AssessmentFindings"
// import InterventionsTabContent      from "./tabs/InterventionsTab"

import OPQRSTTabContent from "./tabs/OPQRST"
import SampleTabContent from "./tabs/SampleTab"
import VitalsTabContent from "./tabs/VitalsTab"
import classNames from "classnames"

export default function RaterInfo({defaultTab = "vitals"}) {
    const [activeTab, setActiveTab] = useState(defaultTab)

    const activateTab = (tab) => {
        if (activeTab !== tab) setActiveTab(tab)
    }
    const isActiveTab = (tab) => tab === activeTab

    return (
        <>
            <Nav tabs>
                <NavItem>
                    <NavLink
                        className={classNames({active: isActiveTab('interventionsStatus') })}
                        onClick={() => activateTab('interventionsStatus')}
                    >
                        Interventions Status
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className={classnames({ active: isActiveTab('vitals') })}
                        onClick={() => activateTab('vitals')}
                    >
                        Vitals
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className={classnames({ active: isActiveTab('sample') })}
                        onClick={() => activateTab('sample')}
                    >
                        SAMPLE
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className={classnames({ active: isActiveTab('opqrst') })}
                        onClick={() => activateTab('opqrst')}
                    >
                        OPQRST
                    </NavLink>
                </NavItem>
                {/* <NavItem>
                    <NavLink
                        className={classnames({ active: isActiveTab('callouts') })}
                        onClick={() => activateTab('callouts')}
                    >
                        Assessment Findings
                    </NavLink>
                </NavItem> */}
            </Nav>
            <TabContent activeTab={activeTab}>
                <TabPane tabId="interventionsStatus">
                  <div>Interventions Status coming soon</div>
                </TabPane>
                <TabPane tabId="vitals" >
                    <VitalsTabContent />
                </TabPane>
                <TabPane tabId="sample" >
                    <SampleTabContent />
                </TabPane >
                <TabPane tabId="opqrst">
                    <OPQRSTTabContent />
                </TabPane>
                {/* <TabPane tabId="callouts" >
                    <AssessmentFindingsTabContent />
                </TabPane> */}
            </TabContent>
        </>
    );
}