import { useEffect, useState } from "react"

import {
    Button,
    Card,
    CardBody,
    CardHeader,
    Col,
    Collapse,
    Container,
    Nav,
    NavItem,
    NavLink,
    Row,
    TabContent,
    TabPane
} from "reactstrap"

import classnames from "classnames"

import { usePrefs }    from "../contexts/PreferencesContext"
import { useScenario } from "../contexts/ScenarioContext"

import ChevronIcon from "./icons/ChevronIcon"

import Panel from "./Panel"

const InterventionOutline = ({ category }) => {

    const { scenario, setCurrIntv, setShowIntvSelection } = useScenario()
    const [intvCatMenu, setIntvCatMenu] = useState(null)

    useEffect(() => {
            const menu = scenario.interventionMenus.find(im => im.id === category)
            if (!!menu) {
                setIntvCatMenu(menu)
                setCurrIntv(null)
            } else {
                setIntvCatMenu(null)
                setCurrIntv(null)
            }
        }, [category]
    )

    const handleInterventionSelection = (event, item) => {
        setShowIntvSelection(false)
        setCurrIntv(item.id)
    }

    return (
        <ul className="interventions-outline">
            {!!intvCatMenu && intvCatMenu.items.map(item => (
                <li
                    key={item.id}
                    className={classnames([`menu-depth-${item.depth}`, (item.type === "heading") ? "menu-heading" : "intervention"])}
                >
                    {item.type === "intervention"
                        ? <Button
                            color="link"
                            size="md"
                            className="intervention-btn"
                            onClick={(event) => handleInterventionSelection(event, item)}
                        >
                            {item.label}
                        </Button>
                        : <span>{item.label}</span>
                    }
                </li>
            ))}
        </ul>
    )
}

const InterventionTabs = () => {
    const { scenario, currIntvCat, setCurrIntvCat } = useScenario()

    return (
        <>
            <Nav tabs>
                {scenario.interventionMenus.map(menu => (
                    <NavItem key={menu.id}>
                        <NavLink
                            className={classnames({ active: currIntvCat === menu.id })}
                            onClick={() => setCurrIntvCat(menu.id)}
                        >
                            {menu.label}
                        </NavLink>
                    </NavItem>
                ))}
            </Nav>
            <TabContent activeTab={currIntvCat}>
                {scenario.interventionMenus.map(menu => (
                    <TabPane tabId={menu.id} key={menu.id} >
                        <InterventionOutline category={menu.id} />
                    </TabPane>
                ))}
            </TabContent>
        </>
    )
}

const VerticalInterventionTabs = () => {
    const { scenario, currIntvCat, setCurrIntvCat } = useScenario()

    return (
        <>
            <Container className="vert-tabs-container">
                <Row className="vert-tabs-row">
                    <Col md="3" className="vert-tabs-col">
                        <ul className="vert-tabs">
                            {scenario.interventionMenus.map(menu => (
                                <li key={menu.id}>
                                    <button
                                        className={classnames("intv-cat-btn", { active: currIntvCat === menu.id })}
                                        onClick={() => setCurrIntvCat(menu.id)}
                                    >
                                        {menu.label}
                                    </button>
                                </li>
                            ))}
                        </ul>
                    </Col>
                    <Col md="9" className="vert-intv-menu">
                        <TabContent activeTab={currIntvCat} className="vert-intv-menu-content">
                            {scenario.interventionMenus.map(menu => (
                                <TabPane tabId={menu.id} key={menu.id}  className="vert-tabs-pane">
                                    <InterventionOutline category={menu.id} />
                                </TabPane>
                            ))}
                        </TabContent>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

const InterventionsAccordian = () => {
    const { scenario, currIntvCat, setCurrIntvCat } = useScenario()
    const toggleCat = (menuID) => {
        if (currIntvCat === menuID) {
            setCurrIntvCat("")
        } else {
            setCurrIntvCat(menuID)
        }
    }
    return (
        <>
            {scenario.interventionMenus.map(menu => (
                <Card key={menu.id}>
                    <CardHeader onClick={() => toggleCat(menu.id)}>
                        {menu.label} <ChevronIcon expand={menu.id === currIntvCat} />
                    </CardHeader>
                    <Collapse isOpen={currIntvCat === menu.id}>
                        <CardBody>
                            <InterventionOutline category={menu.id} />
                        </CardBody>
                    </Collapse>
                </Card>
            ))
            }
        </>
    )
}



