const InterventionsDropdown = () => {
    const { scenario, currIntvCat, currIntv, setCurrIntv} = useScenario()
    const [ menuContents, setMenuContents ] = useState(null)

    const handleInterventionSelection = (event) => {
        setCurrIntv(event.target.value)
    }

    const getIndentation = (depth, label) => {
        let indentation = ""
        while (depth > 1) {
            indentation += " &nbsp; "
            depth--;
        }
        indentation += label;
        return {__html: indentation}
    }

    useEffect(() => {
        if (null === currIntvCat) {
            setMenuContents(null)
        } else {
            // console.log(currIntvCat)
            const contents = scenario.interventionMenus.find(rec => rec.id === currIntvCat)
            console.log("contents", contents)
            setMenuContents(contents)
        }
        }, [currIntvCat]
    )

    if (null === menuContents) {
        return (<div></div>)
    }
    return (
        <select onChange={handleInterventionSelection} value={currIntv || ""}>
            <option value="" disabled> - - - </option>
            {menuContents.items.map(item => (
                <option
                    key={item.id}
                    value={item.id}
                    disabled={item.type === "heading"}
                    dangerouslySetInnerHTML={getIndentation(item.depth, item.label)}
                >
                </option>
            ))}
        </select>
    )
}

