import React, { useContext } from "react"


import { ScenarioContext } from "../../contexts/ScenarioContext"

import MDBModal from "./MDBModal"

import RaterInfo from "../RaterInfo"


const FindingsModal = () => {
    const { showFindings, dismissFindings } = useContext(ScenarioContext)

    return (
        <MDBModal
            name="Findings"
            show={showFindings}
            dismiss={dismissFindings}
        >
            <RaterInfo />
        </MDBModal>
    )
}


export default FindingsModal