
import {ASSESS, ASSESS_OPTION, AWO, DECISION} from "@meddbriefer/scenario-data/constants.js"

import { useScenario } from "../contexts/ScenarioContext"

import CheckBox from "./CheckBox"
import CallOutText from "./CallOutText"
import CallOutIcon from "./icons/CallOutIcon"

const getLabel = (item) => ("abbrLabel" in item) ? item.abbrLabel : item.label


const CheckListItem = ({step}) => {
    const {
        isChecked,
        setChecked,
        toggleChecklistItem,
        setCurrentAssessmentFinding,
    } = useScenario()

    const radioClickHandler = (event, radioGroup, selectedRadio) => {
        const selectedID = selectedRadio.id
        const otherOptionIDs = radioGroup.items.map(item => item.id).filter(id => id !== selectedID)
        // console.log("selectedID", selectedID)
        // console.log("otherOptionIDs", otherOptionIDs)
        // console.log("currentTargetValue", event.currentTarget.value)
        otherOptionIDs.forEach(otherID => setChecked(otherID, false))
        toggleChecklistItem(selectedRadio)
        if (radioGroup.type === AWO) {
            setCurrentAssessmentFinding(selectedID)
        }
    }

    const assessmentClickHandler = (item) => {
        toggleChecklistItem(item)
        setCurrentAssessmentFinding(item.id)
    }

    switch (step.type) {
        case ASSESS:
            return (
                <>
                <CheckBox
                    step={step}
                    clickHandler={() => assessmentClickHandler(step)}
                >
                </CheckBox>
                <span>
                    <CallOutIcon assessment={step} />
                    {isChecked(step.id) && <CallOutText assessment={step} /> }
                </span>
                </>
            )
        case AWO:
        case DECISION:
            return (
                <>
                <i>{step.label}</i> {step.type === AWO && <CallOutIcon assessment={step} />}
                <br/>
                &nbsp; &nbsp;
                {step.items.map(item => (
                    <label key={item.id}>
                        <input
                            type="radio"
                            name={step.id}
                            checked={isChecked(item.id)}
                            value={item.id}
                            onChange={(event) => radioClickHandler(event, step, item) }
                        />
                        &nbsp; {getLabel(item)} &nbsp;
                    </label>
                ))}
                <br/>
                    {step.items.map(item => (
                        isChecked(item.id) && item.type === ASSESS_OPTION && <CallOutText key={item.id} assessment={item} />
                    ))}
                </>
            )
        case "prompt":
            return (<i>{step.label}</i>)
        default:
            // case 'required-action':
            return (
                <CheckBox
                    step={step}
                    clickHandler={() => toggleChecklistItem(step)}
                />
            )
        }

}


export default CheckListItem;